@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Caveat:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
body {
  margin: 0;
  box-sizing: border-box;
  padding: 0;
  background-color: #0a192f;;
  color:white;
  font-family: 'Roboto', sans-serif;
}

a{
    text-decoration: none;
    color:inherit;
}

.avatar-img{
    border-radius: 50%;
    width:200px;
}
.container{
    width: 90%;
    margin: 0 auto;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;

}

.userInfo{
    width:60%;
    margin: 0 auto;
    display:flex;
    flex-direction: row;
    justify-content: space-evenly;
    text-align: center;
    align-items: center;
}


.text-gray{color:darkslategray}

.text-center{
    text-align: center;
}

.credit{
    margin-top:4rem;
    margin-bottom: 2rem;
    text-align: center;
}

input{
    padding: 0.25rem;
}
button{
    padding:0.25rem 1rem;
    border:1px solid green;
    color:white;
    background-color: transparent;
}

.green-text{
    color:#64ffda;
}

.underline{
    text-decoration: underline;
}

.repos{
    display:grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap:3rem;
    gap:3rem;
}

.repo{
    display:flex;
    flex-direction: column;
    justify-content: space-between;
    border-radius: 10px;
    padding:1rem 1.5rem;
    height:100%;
    color:inherit;
    background-color:#303C55;
    margin-bottom:1rem;
}

.repo-header p{
    font-size:12px;
}


.repo-details{
    display:flex;
    flex-direction: row;
    justify-content: space-between;
    font-size:13px;
    align-items: center;

}

.icon{
    width:15px;
}
@media only screen and (max-width: 900px) {
    .repos{
        grid-template-columns: repeat(2, 1fr);
    }

    .userInfo{
        width: 95%;
        flex-direction: column;
    }
}

@media only screen and (max-width: 600px) {
    .repos{
        grid-template-columns: 1fr;
    }

    .container h1{
        font-size: 25px;
    }

    .userInfo{
        width: 95%;
        flex-direction: column;
    }
}


